body {
  margin: 0;
  font-family: 'Cairo', sans-serif !important;
  background-color: var(--mainColor);

}
:root{
  --mainColor:#f1f1f1
}
*{
  box-sizing: border-box !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.reduce{ border-radius:30px !important; -webkit-border-radius:30px !important; -moz-border-radius:30px !important; -ms-border-radius:30px !important; -o-border-radius:30px !important; }
.backGrounds{  background-color: #f1f1f1 !important;}
.click{cursor: pointer;}
.subTitle{
  font-size: 20px;
    font-weight: 700;
}
.imgCard{    width: 70px;}
.fit{width: fit-content;}
.white{background-color: white !important;}
.gold{color: #ffd700;}
.big:hover{
  transition: .3s;
  transform: scale(1.5);
  -webkit-transform: scale(1.5);
  -moz-transform: scale(1.5);
  -ms-transform: scale(1.5);
  -o-transform: scale(1.5);
  -webkit-transition: .3s;
  -moz-transition: .3s;
  -ms-transition: .3s;
  -o-transition: .3s;
}
.rot:hover{
  transition: .2s;
  transform: scale(1.1);
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -ms-transform: scale(1.1);
  -o-transform: scale(1.1);
  -webkit-transition: .2s;
  -moz-transition: .2s;
  -ms-transition: .2s;
  -o-transition: .2s;
}
.shadows{box-shadow: -10px 17px 27px rgb(0 0 0 / 15%)}
.links span{
  margin: 0 10px !important;
  cursor: pointer;
  padding: 10px;
}
.links span:hover{
  background-color: white;
}
.backG{
  background-color: #00917f !important;
}
.iiiiii img{
  width: 75% !important;
}
.rrrr{
  cursor:pointer;
  transform:translateY(-50%);
  position:absolute;
  top:50%;
  right:-4%;
  -webkit-transform:translateY(-50%);
  -moz-transform:translateY(-50%);
  -ms-transform:translateY(-50%);
  -o-transform:translateY(-50%);

}
.llll{
  cursor:pointer;
  transform:translateY(-50%);
  position:absolute;
  top:50%;
  left:-4%;
  -webkit-transform:translateY(-50%);
  -moz-transform:translateY(-50%);
  -ms-transform:translateY(-50%);
  -o-transform:translateY(-50%);

}
.bord{
  border-bottom: 1px solid !important;
}
.in{
  height: 38px;
  max-width: 50%;
  border: none;
  outline: none;
}

.but{
  border-radius: 0px 10px 10px 0px;
  height: 38px;
  background-color: white;
}
.hov:hover{
  font-weight: 800;
  transform: scale(1.05);
  -webkit-transform: scale(1.05);
  -moz-transform: scale(1.05);
  -ms-transform: scale(1.05);
  -o-transform: scale(1.05);
}
.reduces{
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}